.slider .slick-dots button {
    border-radius: 50%;
}


.slider .slick-dots  {
    display: flex;
    justify-content: center;
    align-items: center;
    &:last-child {
        margin-right: 0;
    }
    & li {
        list-style: none;
        margin: 0 9px;
        &.slick-active button {
            background-color: transparent;
            background-image: url('./home.svg');
            border-radius: 0;
            width: 13px;
            height: 12px;
            outline: none;
        }
        }
    
    & button {
        font-size: 0;
        width: 12px;
        height: 12px;
        // border-radius: 50%;
        border: none;
        background-image: linear-gradient(to top, lightgrey 0%, lightgrey 1%, #e0e0e0 26%, #efefef 48%, #d9d9d9 75%, #bcbcbc 100%);
        outline: none;
        cursor: pointer;
    }
    
}




